<template>
  <div class="home" :style="{ backgroundImage: 'url(' + background_image + ')' }">
    <div class="header">
      <div class="wrapper">
        <h1>Gedichte von Hans Josef Rommerskirchen</h1>

        <div class="lead-text">
          <vue-typer
            :text="zitate"
            :repeat='Infinity'
            initial-action='typing'
            :pre-type-delay='70'
            :type-delay='100'
            :pre-erase-delay='4000'
            :erase-delay='1000'
            erase-style='select-all'
            :erase-on-complete='false'
            caret-animation='blink'>
          </vue-typer>
        </div>

        <SearchBar :tags="randomTags" @filter="filter" />
      </div>
    </div>

    <div class="container">
      <div class="item" v-for="(flowitem, index) in flowbook" :key="index">
        <router-link :to="{ path: '/page', query: { item: flowitem.id }}">
          <FlowItem :align="alignSide(index)" :state="index === 0" :title="flowitem.title" :author="flowitem.author" :date="flowitem.date" />
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import { VueTyper } from 'vue-typer'
  import SearchBar from '@/components/SearchBar.vue'
  import FlowItem from '@/components/FlowItem.vue'

  export default {
    name: 'HomeView',
    components: {
      VueTyper,
      FlowItem,
      SearchBar
    },
    data: function(){
      return {
        flowbook: this.$root.flowbook,
        filterQuery: '',
        background_image: 'https://streemlead.com/cdn/wordflow/old-books-1-blurred.png',
        zitate: [
          `"Wer zu lesen versteht, besitzt den Schlüssel zu großen Taten, zu unerträumten Möglichkeiten."
- Aldous Huxley -`,
          `"Nichts verscheuchte böse Träume schneller als das Rascheln von bedrucktem Papier."
- Cornelia Funke -`
        ]
      }
    },
    computed: {
      randomTags: function(){
        var tags = [];
        for (var i = 0; i < this.$root.flowbook.length; i++){
            var shuffledTags = this.shuffleArray(this.$root.flowbook[i].tags);
            for (var b = 0; b < shuffledTags.length; i++){
              if (!tags.includes(shuffledTags[b])){
                tags.push(shuffledTags[b]);
                break;
              }
            }
            if (tags.length == 5) break;
        }

        return tags;
      }
    },
    methods: {
      alignSide: function(index){
        if (index%2 == 0){
          return 'left';
        } else {
          return 'right';
        }
      },
      shuffleArray: function(array) {
        for (var i = array.length - 1; i > 0; i--) {
            var j = Math.floor(Math.random() * (i + 1));
            var temp = array[i];
            array[i] = array[j];
            array[j] = temp;
        }

        return array;
      },
      filter: function(tag){
        if (tag === 'Zufällig'){
          var random =  Math.floor(Math.random() * this.$root.flowbook.length);
          this.$router.push({ path: '/page', query: { item: random } })
        } else {
          this.filterQuery = tag;

          var book = [];
          for (var i = 0; i < this.$root.flowbook.length; i++){
            for (var b = 0; b < this.$root.flowbook[i].tags.length; b++){
              if (this.$root.flowbook[i].tags[b].toLowerCase().includes(this.filterQuery.toLowerCase()) ||
                  this.filterQuery.toLowerCase().includes(this.$root.flowbook[i].tags[b].toLowerCase())){

                    book.push(this.$root.flowbook[i]);
                    break;

              }
            }
          }

          this.flowbook = book;
          this.$forceUpdate();
        }
      }
    }
  }
</script>



