<template>
    <div
    data-aos="fade-up"
    data-aos-delay="50"
    data-aos-duration="1000"
    data-aos-easing="ease-in-out">
        
            <div v-if="align === 'left'" class="flowitem d-flex flex-column" @mouseover="toggleItem(true)" @mouseout="toggleItem(false)">
                <div class="d-flex">
                    <i class='bx bx-message-square-dots icon' :class="{ 'active': isActive, 'bx-tada': isActive }"></i>
                    <span class="title" style="text-align: left; padding-left: 20px;">
                        {{ title }}
                    </span>
                </div>
                <span class="details" style="text-align: left;">- {{ author }}, {{ date }} -</span>
            </div>
            <div v-else class="flowitem d-flex flex-column" @mouseover="toggleItem(true)" @mouseout="toggleItem(false)">
                <div class="d-flex flex-row-reverse">
                    <i class='bx bx-message-square-dots icon' :class="{ 'active': isActive, 'bx-tada': isActive }" style="text-align: right;"></i>
                    <span class="title" style="text-align: right; padding-right: 20px;">
                        {{ title }}
                    </span>
                </div>
                <span class="details" style="text-align: right;">- {{ author }}, {{ date }} -</span>
            </div>
  </div>
</template>




<script>
  export default {
    name: 'FlowItem',
    data: function(){
        return {
            isActive: false
        }
    },
    props: {
        title: String,
        author: String,
        date: String,
        align: String,
        state: Boolean
    },
    methods: {
        toggleItem: function(state){
            this.isActive = state;
        }
    },
    mounted: function(){
        this.$nextTick(function(){
            this.isActive = this.state;
        });
    }
  }
</script>




