<template>
    <div class="searchbar"
             data-aos="zoom-in"
             data-aos-delay="50"
             data-aos-duration="1000"
             data-aos-easing="ease-in-out">

                <h2>Entdecken</h2>
                <b-input-group class="searchcontrols">
                    <b-form-input placeholder="Suchen" v-model="searchQuery"></b-form-input>
                    <b-input-group-append>
                    <b-button variant="info" @click="filter(searchQuery)"><i class='bx bx-search-alt bx-tada'></i></b-button>
                    </b-input-group-append>
                </b-input-group>
                <div class="d-flex flex-wrap tags justify-content-center">
                    <div><b-button @click="filter('Zufällig')" class="tag" size="sm" variant="outline-dark">#Zufällig</b-button></div>
                    <div v-for="(tag, index) in tags" :key="index">
                        <b-button @click="filter(tag)" class="tag" size="sm" variant="outline-dark">#{{ tag }}</b-button>
                    </div>
                </div>
    </div>
</template>

<script>

  export default {
    name: 'SearchBar',
    components: {
    },
    props: {
        tags: Array
    },
    data: function(){
      return {
          searchQuery: ''
      }
    },
    methods: {
        filter: function(tag){
            console.log('filter: ' + tag);
            this.searchQuery = tag;
            this.$emit('filter', tag);
        }
    }
  }
</script>



