<template>
    <div
    data-aos="fade-up"
    data-aos-delay="50"
    data-aos-duration="1000"
    data-aos-easing="ease-in-out">
    <div class="wordflow">
      <h2 class="">{{ title }}</h2>

      <div v-if="type === 'fading'">
        <div v-for="(text, text_index) in texts" :key="text_index">
          <span class="d-block text" :class="{ hidden: !text.isActive, animate__animated: text.isActive, animate__fadeInUp: text.isActive }">
            {{ text.content }}
          </span>
        </div>
      </div>

      <p class="d-block" :class="{ hidden: !authorIsActive, animate__animated: authorIsActive, animate__fadeInLeft: authorIsActive }">- {{ author }}, {{ date }}</p>

      <vue-typer
        v-if="type === 'typer'"
        class="text"
        :text="text[0]"
        :repeat='0'
        initial-action='typing'
        :pre-type-delay='2000'
        :type-delay='70'
        caret-animation='blink' />
    </div>
  </div>
</template>




<script>
  import { VueTyper } from 'vue-typer'

  export default {
    name: 'WordFlow',
    props: {
      title: String,
      author: String,
      date: String,
      type: String,
      text: String
    },
    data: function(){
      return {
        authorIsActive : false
      }
    },
    computed: {
      texts: function(){
        var textReturn = [];
        if (this.text !== '' && this.text !== undefined){
          var textSplit = this.text.split("\n");
          for (var i = 0; i < textSplit.length; i++) textReturn.push({ isActive: false, content: textSplit[i] });
          console.log(textSplit);
        }
        return textReturn;
      }
    },
    components: {
      VueTyper
    },
    methods: {
    },
    mounted: function(){
      var context = this;
      var index = 0;

      var intervalFunction = setInterval(function(){
        if (index < context.texts.length){
          context.texts[index].isActive = true;
          context.$forceUpdate();
          index += 1;
        } else {
          context.authorIsActive = true;
          clearInterval(intervalFunction);
        }
      }, 2500);
    }
  }
</script>




<style scoped>
  .wordflow p {
    font-style: italic;
    margin-top: 40px;
    padding-left: 0px;
    font-size: 18px;
    text-align: left;
  }
  .wordflow h2 {
    margin-bottom: 20px;
  }
  .wordflow .text {
    font-size: 20px;
    min-height: 20px;
  }
  .wordflow {
    margin: auto !important;
  }
  .hidden {
    opacity: 0;
  }
</style>
